.fz-invoice-form {
	padding: 15px 0 0 0;
	max-width: none;
}

.fz-invoice-sums {
	margin-bottom: 48px;
}

.fz-invoice-items,
.fz-invoice-row {
	padding: 0 24px;
}

.fz-invoice-items {
	overflow: auto;
}

.fz-invoice-items__row__cell__item {
	display: block;
}

.fz-invoice-sum {
	padding: 24px 0;
	font-weight: bold;
}

.fz-new-contact-lnk {
	line-height: 32px;
	padding-left: 12px;
}

@media only screen and (min-width: 768px) {
	.fz-invoice-row--sum {
		display: flex;
		flex-flow: row wrap;
		justify-content: center;
	}
}

@media only screen and (min-width: 992px) {
	.fz-invoice-items {
		padding: 0 0;
	}
	
	.fz-invoice-items__row {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
	}

	.fz-invoice-items__row__cell-group {
		display: flex;
		flex: 1;
		width: 33%;
		min-width: 370px;
	}

	.fz-invoice-items__row__cell-group__name {
		min-width: 200px;
	}

	.fz-invoice-items__row__cell-group__sums {
		min-width: 0;
		margin-left: 15px;
	}

	.fz-invoice-items__row__cell {
		margin-right: 15px;
	}

	.fz-invoice-items__row__cell--name {
		flex: 1;
		min-width: 200px;
	}

	.fz-invoice-items__row__cell--narrow {
		width: 80px;
		min-width: 80px;
	}

	.fz-invoice-items__row__cell--price {
		min-width: 80px;
	}

	.fz-invoice-items__row__cell--sum {
		flex: 1;
	}

	.fz-invoice-items__row__cell--delete {
		padding: 0 5px 0 5px;
		margin: 0 10px;
	}

	.fz-invoice-items__row:not(.fz-invoice-items__row--header)
		.fz-invoice-items__label {
		display: inherit;
		visibility: collapse;
		line-height: 0;
		height: 0;
	}
}
