.fz-overview-table-wrapper {
	overflow: auto;
	max-height: 400px;
	margin-bottom: 20px;
}

.fz-overview-table {
	display: table;
	border: none;
	width: 100%;
	table-layout: fixed;
	border-collapse: collapse;
	border-spacing: 0;
	line-height: 1.42857;
	color: #2f3941;
	font-size: 14px;
}

.fz-overview-table__row {
	display: table-row;
	transition: background-color 0.1s ease-in-out;
	border-bottom: 1px solid #e9ebed;
	height: 40px;
	vertical-align: top;
	box-sizing: border-box;
}

.fz-overview-table__row--group {
	height: 32px;
	line-height: 1.66667;
	font-size: 12px;
	background-color: #f8f9f9;
}

.fz-overview-table__row--header {
	border-bottom-color: #d8dcde;
	height: 48px;
	vertical-align: bottom;
	font-weight: 600;
}

.fz-overview-table__row__cell {
	display: table-cell;
	transition: border-color 0.25s ease-in-out, box-shadow 0.1s ease-in-out;
	padding: 10px 12px;
	box-sizing: border-box;
}

.fz-overview-table__row__cell--center {
	text-align: center;
}

.fz-overview-table__row__cell--nowrap {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.fz-overview-table__row--group .fz-overview-table__row__cell {
	padding: 6px 10px;
}

.fz-explanatory-notes {
	margin: 0 0 0 0;
	padding: 0;
	list-style-type: none;
}
